import React from "react"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

export default ({
  type,
  title,
  description,
  eyecatch,
  pagePath,
}) => {
  const { site, profilePic } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            social {
              twitter
            }
            siteUrl
          }
        }
        profilePic: file(relativePath: {eq: "profile-pic.jpg"}) {
          childImageSharp {
            original {
              src
              width
              height
            }
          }
        }
      }
    `
  )

  const ogImage = eyecatch || profilePic.childImageSharp.original

  const metaDescription = description || site.siteMetadata.description
  const pageTitle = title ? `${title} | ${site.siteMetadata.title}` : site.siteMetadata.title
  const pageUrl = `${site.siteMetadata.siteUrl}${pagePath || '/'}`
  const ogImageUrl = `${site.siteMetadata.siteUrl}${ogImage.src}`
  const ogType = type === "blog" ? "article" : "website"

  return (
    <Helmet
      htmlAttributes={{
        lang: `ja`,
      }}
      title={pageTitle}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          property: `og:title`,
          content: pageTitle,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: ogType,
        },
        {
          property: `og:url`,
          content: pageUrl,
        },
        {
          property: `og:image`,
          content: ogImageUrl,
        },
        {
          property: `og:image:width`,
          content: ogImage.width,
        },
        {
          property: `og:image:height`,
          content: ogImage.height,
        },
        {
          property: `twitter:creator`,
          content: site.siteMetadata.social.twitter,
        },
        {
          property: `twitter:card`,
          content: eyecatch ? `summary_large_image` : `summary`,
        },
        {
          property: `twitter:image`,
          content: ogImageUrl,
        },
      ]}
      link={[
        {
          rel: `canonical`,
          href: pageUrl,
        },
      ]}
    />
  )
}
