import React from "react"

import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import { Link } from "gatsby"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faTwitter, faGithub } from "@fortawesome/free-brands-svg-icons"
import { faAddressCard, faEnvelope } from "@fortawesome/free-regular-svg-icons"

export default () => {
  const data = useStaticQuery(graphql`
  query {
    profilePic: file(relativePath: {eq: "profile-pic.jpg"}) {
      childImageSharp {
        fixed(width: 128) {
          ...GatsbyImageSharpFixed_withWebp
        }
      }
    }
    recommendedPosts: allMarkdownRemark(
      filter: { fields: {slug: {in: [
        "golang-channel-pattern",
        "ddd-in-2019",
        "starting-development-with-ddd",
      ]}}}
    ) {
      edges {
        node {
          excerpt(format: HTML)
          fields {
            urlPath
            jstDate
          }
          frontmatter {
            date
            title
            tags
            eyecatch {
              childImageSharp {
                fluid(maxWidth: 1600) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
        }
      }
    }
  }
  `)
  return (
    <div>
      <aside>
        <div className="has-text-centered">
          <figure className="image is-128x128 is-inline-block">
            <Img fixed={data.profilePic.childImageSharp.fixed} alt="avatar" />
          </figure>
          <h1 className="title">Ryota Hori</h1>
        </div>
        <div className="content mb-4">
          <p className="mt-5">
            2019年に渋谷のIT企業を退職後、フリーランスで活動しているバックエンドエンジニア。メイン言語はGoで重めなアーキテクチャ設計が得意。「人間は人間がやるべき仕事に集中しよう」が最近のコンセプトで、ドメイン駆動設計やコード生成技術を積極的に取り入れています。
          </p>
        </div>
        <div>
          <ul className="level is-mobile" style={{ 'margin-left': 'auto', 'margin-right': 'auto', 'max-width': 380 }}>
            <li className="level-item">
              <a href="https://twitter.com/hori_ryota" aria-label="twitter account" target="_blank" rel="noopener noreferrer">
                <span className="icon has-text-dark"><FontAwesomeIcon icon={faTwitter} /></span>
              </a>
            </li>
            <li className="level-item">
              <a href="https://github.com/hori-ryota" aria-label="github account"  target="_blank" rel="noopener noreferrer">
                <span className="icon has-text-dark"><FontAwesomeIcon icon={faGithub} /></span>
              </a>
            </li>
            <li className="level-item">
              <Link to={`/about/`} aria-label="about me" >
                <span className="icon has-text-dark"><FontAwesomeIcon icon={faAddressCard} /></span>
              </Link>
            </li>
            <li className="level-item">
              <a href="mailto:hori.ryota@gmail.com" aria-label="contact me by email"  target="_blank" rel="noopener noreferrer">
                <span className="icon has-text-dark"><FontAwesomeIcon icon={faEnvelope} /></span>
              </a>
            </li>
          </ul>
        </div>
      </aside>
      <aside className="mt-6 is-hidden-touch">
        <h1 className="title is-5">Recommended Posts</h1>
        {data.recommendedPosts.edges.map(({ node }) => (
          <article className="card mt-5">
            {node.frontmatter.eyecatch && (
              <Link to={node.fields.urlPath} className="card-image">
                <figure className="image">
                  <Img fluid={node.frontmatter.eyecatch.childImageSharp.fluid} alt={`eyecatch image of ${node.frontmatter.title}`} />
                </figure>
              </Link>
            )}
            <div className="card-content pt-3 px-4 pb-4">
              <Link to={node.fields.urlPath} className="card-image">
                <h1 className="title is-6 mb-0">{node.frontmatter.title}</h1>
              </Link>
              <div className="has-text-right">
                <time className="is-size-7" dateTime={node.fields.jstDate}>{node.fields.jstDate}</time>
              </div>
            </div>
          </article>
        ))}
      </aside>
      <aside className="mt-6">
        <h1 className="title is-5">Categories</h1>
        <div className="content">
          <ul>
            <li>
              <Link to={`/tags/Development/`} className="has-text-dark">
                Development
              </Link>
            </li>
            <li>
              <Link to={`/tags/Output/`} className="has-text-dark">
                Output
              </Link>
            </li>
          </ul>
        </div>
      </aside>
    </div>
  )
}
