import React from "react"

import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import { Link } from "gatsby"

export default () => {
  const data = useStaticQuery(graphql`
  query {
    recommendedPosts: allMarkdownRemark(
      filter: { fields: {slug: {in: [
        "golang-channel-pattern",
        "ddd-in-2019",
        "starting-development-with-ddd",
      ]}}}
    ) {
      edges {
        node {
          excerpt(format: HTML)
          fields {
            urlPath
            jstDate
          }
          frontmatter {
            date
            title
            eyecatch {
              childImageSharp {
                fixed(width: 128) {
                  ...GatsbyImageSharpFixed_withWebp
                }
              }
            }
          }
        }
      }
    }
  }
  `)
  return (
    <footer className="footer">
      <div className="container">
        <div className="columns is-centered">
          <aside className="column is-half content pt-1">
              <a className="twitter-timeline"
                data-lang="en"
                data-theme="light"
                data-chrome="transparent,noheader"
                data-width="425"
                data-height="570"
                href="https://twitter.com/hori_ryota?ref_src=twsrc%5Etfw"
              >Tweets by hori_ryota</a>
          </aside>
          <aside className="column is-half">
            <h1 className="title is-5">Recommended Posts</h1>
            {data.recommendedPosts.edges.map(({ node }) => (
              <article className="media">
                {node.frontmatter.eyecatch && (
                  <Link to={node.fields.urlPath} className="media-left">
                    <figure className="image is-16by9 pt-0">
                      <Img fixed={node.frontmatter.eyecatch.childImageSharp.fixed} alt={`eyecatch image of ${node.frontmatter.title}`} />
                    </figure>
                  </Link>
                )}
                <div className="media-content">
                  <Link to={node.fields.urlPath} className="media-left">
                    <h1 className="title is-6">{node.frontmatter.title}</h1>
                  </Link>
                  <div className="has-text-right">
                    <time className="is-size-7" dateTime={node.fields.jstDate}>{node.fields.jstDate}</time>
                  </div>
                </div>
              </article>
            ))}
          </aside>
        </div>
      </div>
    </footer>
  )
}
